body {
  letter-spacing: -.025em;
  word-break: keep-all;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, Helvetica Neue, Segoe UI, Apple SD Gothic Neo, Noto Sans KR, Malgun Gothic, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.6em;
}

.main {
  color: #fff;
  background-color: #141414;
  min-height: 100vh;
}

.main > .ct-section-inner-wrap {
  width: 100%;
  padding: 0;
}

.section > .ct-section-inner-wrap {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: calc(100% - 30px);
  max-width: 730px;
  margin: 0 auto;
  padding: 160px 0;
  display: flex;
}

.headline-box {
  width: calc(100% - 30px);
  max-width: 445px;
  margin-right: auto;
}

.headline-box__index {
  color: #d91a83;
  font-family: PPMonumentExtended, -apple-system, BlinkMacSystemFont, system-ui, Roboto, Helvetica Neue, Segoe UI, Apple SD Gothic Neo, Noto Sans KR, Malgun Gothic, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, sans-serif;
}

.headline-box__head {
  padding: .5em 0;
  font-size: 48px;
  font-weight: bold;
  line-height: 1.3;
}

.headline-box__body {
  font-size: 16px;
  font-weight: normal;
  line-height: 1.6em;
}

.package-slider__card {
  background-color: #fff;
  width: 540px;
}

.package-slider__card a {
  color: #111;
}

.package-slider__card h3 {
  text-transform: capitalize;
  font-family: PPMonumentExtended, -apple-system, BlinkMacSystemFont, system-ui, Roboto, Helvetica Neue, Segoe UI, Apple SD Gothic Neo, Noto Sans KR, Malgun Gothic, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, sans-serif;
  font-size: 28px;
  font-weight: 700;
}

.package-slider .price-tag__item {
  display: flex;
}
